import Vue from 'vue'
import Router from 'vue-router'
import Hdxx from '@/pages/hdxx'//
import Hdxxxq from '@/pages/hdxxxq'//
import Hxxx from '@/pages/hxxx'//
import Hxxxxq from '@/pages/hxxxxq'//
import Swqx from '@/pages/swqx'//
import Zsyz from '@/pages/zsyz'//
import Zsyzxq from '@/pages/zsyzxq'//
import Gkhb from '@/pages/gkhb'//
import Gqdt from '@/pages/gqdt'//
import Hydt from '@/pages/hydt'//
import Cbdt from '@/pages/cbdt'//
import Axsc from '@/pages/axsc'//
import Axscxq from '@/pages/axscxq'//
import Xmgc from '@/pages/xmgc'//
import Xmgcxq from '@/pages/xmgcxq'//
import Yjzj from '@/pages/yjzj'//
import Yjzjxq from '@/pages/yjzjxq'//
import Yjya from '@/pages/yjya'//
import Yjyaxq from '@/pages/yjyaxq'//
import Yjwz from '@/pages/yjwz'//
import Yjwzxq from '@/pages/yjwzxq'//

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/hdxx', component: Hdxx },
    { path: '/hdxxxq/:id', component: Hdxxxq },
    { path: '/hxxx', component: Hxxx },
    { path: '/hxxxxq/:id', component: Hxxxxq },
    { path: '/swqx', component: Swqx },
    { path: '/zsyz', component: Zsyz },
    { path: '/zsyzxq/:id', component: Zsyzxq },
    { path: '/gkhb', component: Gkhb },
    { path: '/gqdt', component: Gqdt },
    { path: '/hydt', component: Hydt },
    { path: '/cbdt', component: Cbdt },
    { path: '/axsc', component: Axsc},
    { path: '/axscxq/:id', component: Axscxq },
    { path: '/xmgc', component: Xmgc},
    { path: '/xmgcxq/:id', component: Xmgcxq },
    { path: '/yjzj', component: Yjzj},
    { path: '/yjzjxq/:id', component: Yjzjxq },
    { path: '/yjya', component: Yjya},
    { path: '/yjyaxq/:id', component: Yjyaxq },
    { path: '/yjwz', component: Yjwz},
    { path: '/yjwzxq/:id', component: Yjwzxq },
  ]
})



